// tslint:disable:no-magic-numbers
import { Component, ElementRef, ViewChild } from "@angular/core";
import { StatsService } from "../../../providers/loans/stats.service";
import { NavController } from "ionic-angular";
import { Filters } from "../../../common/utils/filters";
import { Chart } from "chart.js";

@Component({
  selector: "conversion-dashboard",
  templateUrl: "conversion.html",
})
export class ConversionDashboard {

  @ViewChild("chartEle") chartEle: ElementRef;
  @ViewChild("chartEleAvg") chartEleAvg: ElementRef;

  public totalCount = 0;
  public totalAmount = 0;
  public formatCurrency = Filters.currency;
  public formatCurrencyShort = Filters.formatCurrencyShort;

  public activeConversionChartType = "Count";

  public avgAmountConversion = [8000000, 3000000, 1300000 ];
  public avgUnitCoversion = [80, 30, 13];

  public myAmountConversion = [5500000, 2500000, 800000];
  public myUnitCoversion = [55, 25, 8];

  public myConversion = this.myUnitCoversion;
  public avgConversion = this.avgUnitCoversion;

  public countChartType = false;

  public amtColors = ["#7fccb2", "#45b08c ", "#009966"];
  public cntColors = ["#83c0e8", "#4a9fd7 ", "#0881d1"];

  public circleGrey = "#e6e6e6";

  public chartObj: any;
  public chartObjAvg: any;

  public data = [
    [100, 0, 0, 0],
    [0, 60, 0, 40],
    [0, 0, 30, 70],
  ];

  public charts = {
    datasets: [
      {
        data: this.data[0],
        strokeWidth: 10,
        backgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
        hoverBackgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
      },
      {
        data: this.data[1],
        strokeWidth: 10,
        backgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
        hoverBackgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
      },
      {
        data: this.data[2],
        strokeWidth: 10,
        backgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
        hoverBackgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
      },
    ],
    showValue: true,
    showCurrencyFilter: true,
    labels: ["Lead", "Application", "Funded", ""],
  };

  public dataAvg = [
    [100, 0, 0, 0],
    [0, 60, 0, 40],
    [0, 0, 30, 70],
  ];

  public chartsAvg = {
    datasets: [
      {
        data: this.dataAvg[0],
        strokeWidth: 10,
        backgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
        hoverBackgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
      },
      {
        data: this.dataAvg[1],
        strokeWidth: 10,
        backgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
        hoverBackgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
      },
      {
        data: this.dataAvg[2],
        strokeWidth: 10,
        backgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
        hoverBackgroundColor: [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey],
      },
    ],
    showValue: true,
    showCurrencyFilter: true,
    labels: ["Lead", "Application", "Funded", ""],
  };

  constructor(
    public service: StatsService,
    private nav: NavController,
  ) {
    const base = this;
    Chart.pluginService.register({
      beforeDraw(chart) {
        if (!chart.data.showValue) {
          return;
        }
        const width = chart.chart.width;
        const height = chart.chart.height;
        const ctx = chart.chart.ctx;

        ctx.restore();
        const fontSize = (height / 230).toFixed(2);

        ctx.font = "500 " + fontSize + "em sans-serif";
        ctx.textBaseline = "middle";

        const datasets = chart.data.datasets;

        datasets.forEach( (d, i) => {
          let text = d.data[i];
          if (chart.data.showCurrencyFilter) {
            text = base.formatCurrencyShort(text, true);
          }

          const textWidth = ctx.measureText(text).width;
          const textX = (chart.width / 2 - textWidth) - 5;

          const pieSectionHeight = (height * (100 - chart.options.cutoutPercentage) / 100) / 2 / 3;
          const sectionTop = pieSectionHeight * (i + 1);

          const oneEM = 16; // in px
          const textHeightPX = parseFloat(fontSize) * oneEM;

          const textY = sectionTop
                          - ((pieSectionHeight - textHeightPX)
                          + (pieSectionHeight - textHeightPX) / 2 );

          ctx.fillStyle = "#999";
          ctx.fillText(text, textX, textY);
          ctx.save();

        });
      },
    });
  }
  ngOnInit() {
    this.service.getCompareQuotesConversion("YTD")
    .subscribe((response) => {
      if (response) {
        const average = response.averageConvertedQuotes;
        this.avgAmountConversion = [
          average.quotes.loanAmt ? average.quotes.loanAmt : 0,
          average.applications.loanAmt ? average.applications.loanAmt : 0,
          average.closedLoans.loanAmt ? average.closedLoans.loanAmt : 0,
        ];
        this.avgUnitCoversion = [
          average.quotes.count,
          average.applications.count,
          average.closedLoans.count,
        ];

        const myConv = response.myConvertedQuotes;
        this.myAmountConversion = [
          myConv.quotes.loanAmt ? myConv.quotes.loanAmt : 0,
          myConv.applications.loanAmt ? myConv.applications.loanAmt : 0,
          myConv.closedLoans.loanAmt ? myConv.closedLoans.loanAmt : 0,
        ];
        this.myUnitCoversion = [
          myConv.quotes.count,
          myConv.applications.count,
          myConv.closedLoans.count,
        ];

        this.myConversion = this.myAmountConversion;
        this.avgConversion = this.avgAmountConversion;
        let data =  this.myConversion.map( (d, index) => {
          const arr = [0, 0, 0, 0];
          arr[index] = d;
          arr[3] = this.myConversion[0] - d;
          return arr;
        });
        this.charts.datasets.forEach( (cd, index) => {
          cd.data = data[index];
        });

        data =  this.avgConversion.map( (d, index) => {
          const arr = [0, 0, 0, 0];
          arr[index] = d;
          arr[3] = this.avgConversion[0] - d;
          return arr;
        });
        this.chartsAvg.datasets.forEach( (cd, index) => {
          cd.data = data[index];
        });

        this.loadConversion();
      }
    });
    this.loadConversion();
  }

  public loadConversion() {

      const options = {
        cutoutPercentage: 50,
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: false,
        },
        circumference: 1.5 * Math.PI,
        tooltips: {
          enabled: false,
        },
      };

      if (this.chartObj && "destroy" in this.chartObj) {
        this.chartObj.destroy();
        this.chartObj = null;
      }

      this.chartObj = new Chart(this.chartEle.nativeElement, {
        type: "doughnut",
        data: this.charts,
        options,
      });
      this.chartObjAvg = new Chart(this.chartEleAvg.nativeElement, {
        type: "doughnut",
        data: this.chartsAvg,
        options,
      });
  }

  private changeChart(countChartType) {
    this.myConversion = (countChartType) ? this.myUnitCoversion : this.myAmountConversion;
    this.avgConversion = (countChartType)
                            ? this.avgUnitCoversion
                            : this.avgAmountConversion;

    this.charts.showCurrencyFilter = !(countChartType);
    let data =  this.myConversion.map( (d, index) => {
      const arr = [0, 0, 0, 0];
      arr[index] = d;
      arr[3] = this.myConversion[0] - d;
      return arr;
    });
    this.charts.datasets.forEach( (cd, index) => {
      cd.data = data[index];
      cd.backgroundColor = this.getColors(countChartType);
    });

    this.chartsAvg.showCurrencyFilter = !(countChartType);
    data =  this.avgConversion.map( (d, index) => {
      const arr = [0, 0, 0, 0];
      arr[index] = d;
      arr[3] = this.avgConversion[0] - d;
      return arr;
    });
    this.chartsAvg.datasets.forEach( (cd, index) => {
      cd.data = data[index];
      cd.backgroundColor = this.getColors(countChartType);
    });

    this.loadConversion();
  }

  private openQuotes(quote) {
    this.nav.push("MyQuotes", {
      // params: {},
    });
  }

  private getColors(countChartType: boolean) {
     if (countChartType) {
        return [this.cntColors[0], this.cntColors[1], this.cntColors[2], this.circleGrey];
      }else {
        return [this.amtColors[0], this.amtColors[1], this.amtColors[2], this.circleGrey];
      }
  }
}



