// tslint:disable:no-magic-numbers
import { Component, ElementRef, ViewChild } from "@angular/core";
import { Chart } from "chart.js";
import { HomeService } from "../../../../providers/home.service";
import { Search } from "../../../../common/model/search";
import { NavController } from "ionic-angular";

const UNIT_BASE_COLOR: string = "#e8e7e7";
const UNIT_PROGRESS_COLOR: string = "#2976c3";
const ANIMATION_TIMING: number = 1000;
const CHART_WIDTH: number = 140;
const CHART_HEIGHT: number = 140;

@Component({
  selector: "pipeline-status-dashboard",
  templateUrl: "pipeline-status.html",
})
export class PipelineStatusDashboard {

  text: string;

  @ViewChild("chartEle1") chartEle1: ElementRef;
  @ViewChild("chartEle2") chartEle2: ElementRef;
  @ViewChild("chartEle3") chartEle3: ElementRef;
  @ViewChild("chartEle4") chartEle4: ElementRef;

  public chart: any;
  public colors = ["#4ad991", "#45cc89", "#41bf80", "#3db378", "#38a66f", "#349967", "#2b8055"];

  public circleGrey = "#DFDFDF";

  public circlePercentage = 0;

  public total = 0;
  public count = [];

  public charts = [
    {
      datasets: [{
        data: [20, 80],
        backgroundColor: [this.colors[0], this.circleGrey],
        hoverBackgroundColor: [this.colors[0], this.circleGrey],
      }],
      labels: ["Fulfillment", "Others"],
      showPercentage: true,
      borderWidth: 0,
    },
  ];

  public chartMap = {
    fulfillment: {
      types: ["lead", "prospect", "application", "setup", "processing"],
    },
    underwriting: {
      types: ["underwriting"],
    },
    approved: {
      types: ["approved"],
    },
    closing: {
      types: ["clear to close", "closing"],
    },
  };

  public activeCategories = [
    "lead",
    "prospect",
    "application",
    "setup",
    "processing",
    "underwriting",
    "approved",
    "clear to close",
    "closing",
  ].join(",");

  constructor(
    private homeService: HomeService,
    private nav: NavController,
  ) {
    this.text = "PipelineStatusDashboard";
    const base = this;
  }

  ngOnInit() {
    this.homeService.getPipelineCounts().subscribe(
      (response) => {
        let data = response.data.filter((d) => d.count > 0);
        let base = this;
        data = data.filter((d) => (base.activeCategories.indexOf(d.desc.toLowerCase().trim()) > 0));

        this.total = data.reduce((total, d) => total + d.count, 0);
        const mappedData = Object.keys(this.chartMap).map((category, i) => {
          const mapFilter = data
            .filter((d) =>
              this.chartMap[category].types
                .findIndex((type) => type === d.desc.toLowerCase().trim()) > -1);
          let mapData = {
            datasets: [
              {
                data: [0, this.total],
                backgroundColor: [this.colors[i], this.circleGrey],
                hoverBackgroundColor: [this.colors[i], this.circleGrey],
                borderWidth: [0, 0],
              },
            ],
            labels: [category, "Others"],
            showPercentage: true,
            borderWidth: 0,
          };
          mapData = mapFilter.length < 1 ? mapData : mapFilter
            .reduce((obj, d) => {
              const count = obj.datasets ? obj.datasets[0].data[0] + d.count : d.count;
              return {
                datasets: [
                  {
                    data: [count, this.total - count],
                    backgroundColor: [this.colors[i], this.circleGrey],
                    hoverBackgroundColor: [this.colors[i], this.circleGrey],
                    borderWidth: [0, 0],
                  },
                ],
                labels: [d.desc, "Others"],
                showPercentage: true,
              };
            }, {});

          return mapData;
        });

        this.count = mappedData.map((d) => d.datasets[0].data[0]);

        this.loadCharts(mappedData, this.total);
      },
      (error) => {
        // console.log(error);
      });
  }

  public loadCharts(data, total) {
    const base = this;
    data.forEach((d, index) => {
      if (index < 5) {
        this.animate(this["chartEle" + (index + 1)], d.datasets[0].data[0], total);
      }
    });
  }

  public openLoans(status) {
    const searchParams: Search = new Search();
    searchParams.loanStatusType = status;
    this.nav.push("MyLoans", {
      params: {
        searchParams,
      },
    });
  }

  animate(element, count, total) {
    const tween = function(start, end, duration, easing, elem, callback, baseColor, progressColor) {
      const delta = end - start;
      let startTime;
      if (window.performance && window.performance.now) {
        startTime = performance.now();
      } else if (Date.now) {
        startTime = Date.now();
      } else {
        startTime = new Date().getTime();
      }
      const tweenLoop = function (time?) { // tslint:disable-line
        const t = (!time ? 0 : time - startTime);
        const factor = easing(null, t, 0, 1, duration);
        let currVal = Math.round(start + delta * factor);// tslint:disable-line
        if (currVal > end) {
          currVal = end;
        }
        callback(elem, baseColor, progressColor, currVal);
        if (t < duration && currVal < end) {
          requestAnimationFrame(tweenLoop);
        }
      };
      tweenLoop();
    };
    const percent = count / total * 100;

    tween(0, percent, ANIMATION_TIMING,
      (x, t, b, c, d) => {
        return c * (t /= d) * t * t + b;
      }, element.nativeElement, this.canvasInit,
      UNIT_BASE_COLOR, UNIT_PROGRESS_COLOR,
    );
  }

  canvasInit(element, base_color, progress_color, percent) {

    const canvas = element;
    const ctx = canvas.getContext("2d");

    /*FIX FOR IOS RETINA DISPLAYS*/
    const ratio = window.devicePixelRatio || 1;
    canvas.width = CHART_WIDTH * ratio;
    canvas.height = CHART_HEIGHT * ratio;
    canvas.style.width = CHART_WIDTH + "px";
    canvas.style.height = CHART_HEIGHT + "px";
    ctx.scale(ratio, ratio);
    /*FIX FOR IOS RETINA DISPLAYS*/
    const W = canvas.width / ratio;
    const H = canvas.height / ratio;
    const arcWidth = 10;
    const radius = 60;
    const startAngle = Math.PI * 7 / 8;
    const endAngle = startAngle + Math.PI * 5 / 4;
    const centerX = W / 2;
    const centerY = (H / 2) + arcWidth;
    const progressAngle = startAngle + ((percent / 100) * Math.PI * 5 / 4);
    const text1Y = centerY + 15;
    const text2Y = centerY + 27;// tslint:disable-line
    ctx.clearRect(0, 0, W, H);

    ctx.beginPath();
    ctx.strokeStyle = base_color;
    ctx.lineWidth = arcWidth;
    ctx.lineCap = "round";
    ctx.arc(centerX, centerY, radius, startAngle, endAngle, false);
    ctx.stroke();

    ctx.beginPath();
    ctx.strokeStyle = progress_color;
    ctx.lineWidth = arcWidth;
    ctx.lineCap = "round";
    ctx.arc(centerX, centerY, radius, startAngle, progressAngle, false);
    ctx.stroke();

    ctx.font = (radius / 2) + "px 'Lato-Regular'";// tslint:disable-line
    ctx.textAlign = "center";
    ctx.fillStyle = "#666666";
    percent = Math.round(percent);
    ctx.fillText(percent, centerX - 5, centerY);

    ctx.font = (radius / 3.5) + "px 'Lato-Regular'";// tslint:disable-line
    ctx.textAlign = "center";
    ctx.fillStyle = "#666666";
    ctx.fillText("%", centerX + ctx.measureText(percent).width, centerY);
  }

}



