import { Component } from "@angular/core";
import { HomeService } from "../../../../providers/home.service";
import { Filters } from "../../../../common/utils/filters";
import { NavController } from "ionic-angular";
import { Search } from "../../../../common/model/search";

@Component({
  selector: "pipeline-milestone-dashboard",
  templateUrl: "pipeline-milestone.html",
})
export class PipelineMilestoneDashboard {

  pipelineData = [];
  totalCount = 0;
  totalAmount = 0;
  formatCurrency = Filters.currency;
  formatCurrencyShort = Filters.formatCurrencyShort;
  showFilter: boolean = false;
  filterValues: any = {
    type: "Funded",
    rane: "YTD",
  };

  public countChartType = false;
  public avgDays = 6.91;
  public sla = 16;

  constructor(
    private homeService: HomeService,
    private filters: Filters,
    private nav: NavController,
  ) {
  }
  ngOnInit() {
    this.homeService.getPipelineCounts().subscribe(
      (response) => {
        const data = response.data.filter( (d) => d.count > 0);
        this.totalCount = data.reduce( (total, d) => total + d.count, 0 );
        this.totalAmount = data.reduce( (total, d) => total + d.amount, 0 );
        this.pipelineData = data.map((d) => {
          d.countPercent = ((d.count / this.totalCount) * 100).toFixed(0);
          d.amountPercent = ((d.amount / this.totalAmount) * 100).toFixed(0);
          return d;
        });
      },
      (error) => {
        // console.log(error);
     });
  }

  public openLoans(status) {
    const searchParams: Search = new Search();
    searchParams.loanStatusType = status;
    this.nav.push("MyLoans", {
      params: {
        searchParams,
      },
    });
  }

  public toggleFilter(){
    this.showFilter = !this.showFilter;
  }

  public onApply(event) {
    this.filterValues = event;
    this.showFilter = false;
  }

}



