import { NgModule } from "@angular/core";
import { IonicPageModule } from "ionic-angular";
import { Dashboard } from "./dashboard";
import { AlertsDashboardModule,
 } from "../../../components/dashboard/alerts-dashboard/alerts-dashboard.module";
import { MyPipelineDashboardModule,
 } from "../../../components/dashboard/my-pipeline/my-pipeline.module";
import { MyQuotesDashboardModule,
 } from "../../../components/dashboard/my-quotes/my-quotes.module";
import { PerformanceDashboardModule,
 } from "../../../components/dashboard/performance-dashboard/performance-dashboard.module";
import { ConversionDashboardModule,
 } from "../../../components/dashboard/conversion/conversion.module";

@NgModule({
  declarations: [
    Dashboard,
  ],
  imports: [
    AlertsDashboardModule,
    MyPipelineDashboardModule,
    MyQuotesDashboardModule,
    ConversionDashboardModule,
    PerformanceDashboardModule,
    IonicPageModule.forChild(Dashboard),
  ],
  exports: [
    Dashboard,
  ],
})
export class DashboardModule {}



