import {
  Component,
  ViewChild,
  ElementRef,
  HostListener,
} from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  Slides,
  Platform,
  ModalController,
  MenuController,
  Content,
} from "ionic-angular";
import {
  AuthService,
} from "../../../providers/auth.service";
import {
  MenuService,
} from "../../../providers/menu.service";
import {
  AlertModel,
} from "../../../common/model/dashboard/alert.model";
import {
  DashboardService,
} from "../../../providers/dashboard/dashboard.service";
import {
  FeedbackService,
} from "../../../providers/feedback/feedback.service";
import {
  FeedbackMessage,
} from "../../../providers/feedback/feedback.messages";
import moment from "moment";

@IonicPage()
@Component({
  selector: "page-dashboard",
  templateUrl: "dashboard.html",
})
export class Dashboard {

  @ViewChild(Slides) slides: Slides;
  @ViewChild("dashboardHeader") dashboardHeader: ElementRef;
  @ViewChild(Content) content: Content;
  segment: string = "alerts";
  loanCards: AlertModel[] = [];
  taskCards: AlertModel[] = [];
  eventAppointmentCards: AlertModel[] = [];
  editCards: any = [{
    name: "Alerts",
    value: this.loanCards,
  },
  {
    name: "My Pipeline",
    value: [
      {
        name: "Show Section",
        display: true,
      },
    ],
  },
  {
    name: "My Quote",
    value: [
      {
        name: "Show Section",
        display: true,
      },
    ],
  },
  {
    name: "My Tasks",
    value: this.taskCards,
  },
  {
    name: "Events & Appointments",
    value: [
      {
        name: "Show Section",
        display: true,
      },
    ],
  },
  {
    name: "Conversion",
    value: [
      {
        name: "Show Section",
        display: true,
      },
    ],
  },
  {
    name: "Performance",
    value: [
      {
        name: "Show Section",
        display: true,
      },
    ],
  },
  ];
  sections: any = [
    {
      name: "Alerts",
      show: true,
    },
    {
      name: "My Pipeline",
      show: true,
    },
    {
      name: "My Quotes",
      show: true,
    },
    {
      name: "My Tasks",
      show: true,
    },
    {
      name: "Events & Appointments",
      show: true,
    },
    {
      name: "Conversion",
      show: true,
    },
    {
      name: "Performance",
      show: true,
    },
  ];

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private authService: AuthService,
              private modalCtrl: ModalController,
              public platform: Platform,
              public menuService: MenuService,
              public menuCtrl: MenuController,
              private dashboardService: DashboardService,
              public feedback: FeedbackService,
            ) {
    this.init();
  }

  ionViewCanEnter() {
    if (!this.authService.authentication) {
      this.navCtrl.setRoot("LoginPage");
    }
    return true;
  }

  @HostListener("window:resize")
  onResize(event){
    this.content.resize();
  }

  ionViewDidLoad() {
    const slideBoxQuery = ".menu-slide-box";
    const slideBox = this.dashboardHeader.nativeElement.querySelector(
      slideBoxQuery);
    const activeTab = this.dashboardHeader.nativeElement.querySelector(
      ".active-tab");

    if (!slideBox) {
      return;
    }

    slideBox.style.left = (activeTab.getBoundingClientRect().left +
      activeTab.parentNode.scrollLeft - activeTab.parentNode.getBoundingClientRect()
      .left) + "px";
    slideBox.style.width = activeTab.getBoundingClientRect().width +
      "px";
    if (this.platform.is("core") || this.platform.is("tablet")) {
      this.dashboardHeader.nativeElement.parentNode.parentNode.style.position = "absolute";
    }
  }

  ionViewWillEnter() {
    if (this.platform.is("core") || this.platform.is("tablet")) {
      // this.menuService.toggleShow();
      this.menuService.showHideMenu(false);
      this.dashboardHeader.nativeElement.parentNode.parentNode.style.position = "absolute";
    }
  }

  ionViewWillLeave() {
    if (this.platform.is("core") || this.platform.is("tablet")) {
      this.menuService.resetToggle();
      this.dashboardHeader.nativeElement.parentNode.parentNode.style.position = "relative";
    }
  }

  init() {
    // this.loanCards = [];
    // this.taskCards = [];
    // this.eventAppointmentCards = [];
    this.getLoanAlerts();
    this.getTaskDueToday();
    this.getTaskOverdue();
    this.getEventsAndAppointments();
  }

  toggleMenu() {
    this.menuCtrl.toggle();
  }

  getEventsAndAppointments() {
    try {
      this.dashboardService.getEventsAndAppointments().subscribe((response) => {
        this.eventAppointmentCards = this.dashboardService.getEventsAndAppointmentsAlerts(
          response);
      });
    }
    catch (e) {
      this.feedback.showToast(FeedbackMessage.GENERIC_FAILURE.toString(), "error");
    }
  }

  getLoanAlerts() {
    try {
      this.dashboardService.getLoanAlertsCount().subscribe((response) => {
        this.loanCards.push(...this.dashboardService.getLoanAlerts(response));
      });
    }
    catch (e) {
      this.feedback.showToast(FeedbackMessage.GENERIC_FAILURE.toString(), "error");
    }
  }

  private getTaskOverdue() {
    try {
      this.dashboardService.getTodaysTaskOverdue().subscribe(
        (response) => {
          if (response && response.success === true) {
            this.taskCards.push(this.dashboardService.getTaskOverDueAlerts(
              response));
            }
          });
    }
    catch (e) {
      this.feedback.showToast(FeedbackMessage.GENERIC_FAILURE.toString(), "error");
    }
  }

  private getTaskDueToday() {
    try {
      this.dashboardService.getTaskDueToday().subscribe(
        (response) => {
          if (response && response.success === true) {
          this.taskCards.push(...this.dashboardService.getTaskAlerts(
            response));
        }
      });
    }
    catch (e) {
      this.feedback.showToast(FeedbackMessage.GENERIC_FAILURE.toString(), "error");
    }
  }

  private editDashboard() {
    const dashboardEditModal = this.modalCtrl.create("EditDashboard", {
      cards: this.editCards,
      sections: this.sections,
    });
    dashboardEditModal.onDidDismiss((data) => {
      if (!this.platform.is("tablet") && !this.platform.is("core")) {
       this.editCards = data;
       this.updateSegment();
      } else {
       this.sections = data;
     }
    });
    dashboardEditModal.present();
  }

  private updateSegment() {
    const activeSlideNo: number = this.slides.getActiveIndex();
    const allTabsQuery = ".tab-menu";
    const activeTabQuery = ".tab-menu:nth-of-type(" + Number(
      activeSlideNo + 1) + ")";
    const allTabs = this.dashboardHeader.nativeElement.querySelectorAll(
      allTabsQuery);
    const activeTab = this.dashboardHeader.nativeElement.querySelector(
      activeTabQuery);

    const slideBoxQuery = ".menu-slide-box";
    const slideBox = this.dashboardHeader.nativeElement.querySelector(
      slideBoxQuery);

    if (!activeTab) {
      return;
    }

    slideBox.style.left = (activeTab.getBoundingClientRect().left +
      activeTab.parentNode.scrollLeft - activeTab.parentNode.getBoundingClientRect()
      .left) + "px";
    slideBox.style.width = activeTab.getBoundingClientRect().width +
      "px";

    for (let i = 0; i < allTabs.length; i++) {// tslint:disable-line
      allTabs[i].classList.remove("active-tab");
    }
    activeTab.classList.add("active-tab");

    const scrollFromPosition = activeTab.parentNode.scrollLeft;
    const scrollToPosition = activeTab.offsetLeft - 80 -// tslint:disable-line
      activeTab.parentNode.getBoundingClientRect().left;
    const tween = function(start, end, duration, easing) { // tslint:disable-line
      const delta = end - start;
      let startTime;
      if (window.performance && window.performance.now) {
        startTime = performance.now();
      } else if (Date.now) {
        startTime = Date.now();
      } else {
        startTime = new Date().getTime();
      }
      const tweenLoop = function(time ?) { // tslint:disable-line
        const t = (!time ? 0 : time - startTime);
        const factor = easing(null, t, 0, 1, duration);
        activeTab.parentNode.scrollLeft = start + delta * factor;// tslint:disable-line
        if (t < duration && activeTab.parentNode.scrollLeft !== end) {
          requestAnimationFrame(tweenLoop);
        }
      };
      tweenLoop();
    };

    tween(scrollFromPosition, scrollToPosition, 500,// tslint:disable-line
      (x, t, b, c, d) => {
        return c * (t /= d) * t * t + b;
      });
  }
}



