import { Component } from "@angular/core";
import { HomeService } from "../../../providers/home.service";
import { NavController,
         Platform } from "ionic-angular";
import { Filters } from "../../../common/utils/filters";
import {
  QuoteService,
} from "../../../providers/quotes/quote.service";
import {
  FeedbackService,
} from "../../../providers/feedback/feedback.service";
import {
  FeedbackMessage,
} from "../../../providers/feedback/feedback.messages";

@Component({
  selector: "my-quotes-dashboard",
  templateUrl: "my-quotes.html",
})
export class MyQuotesDashboard {

  quotesData = [];
  totalCount = 0;
  totalAmount = 0;
  formatCurrency = Filters.currency;
  formatCurrencyShort = Filters.formatCurrencyShort;

  quotesOrder =  [
     "In Process",
     "Application Started",
     "No Status",
     "No Response",
     "Not Interested",
     "Bad Contact",
     "Do Not Call",
   ];

  public countChartType = false;

  constructor(
    private quoteService: QuoteService,
    private filters: Filters,
    public platform: Platform,
    private nav: NavController,
    public feedback: FeedbackService,
  ) {
  }
  ngOnInit() {
    try {
      this.quoteService.getQuoteStats().subscribe(
        (data) => {
          data = data.filter((d) => d.count);
          this.totalCount = data.reduce( (tot, d) => tot + d.count , 0 );
          this.totalAmount = data.reduce( (tot, d) => tot + d.loanAmt , 0 );
          const quotesData = data.map((d) => {
            d.countPercent = ((d.count / this.totalCount) * 100).toFixed(0);
            d.amountPercent = ((d.loanAmt / this.totalAmount) * 100).toFixed(0);
            return d;
          });
          const quotesDataOrdered = [];

          this.quotesOrder.forEach( (desc) => {
            const quote = quotesData.find((q) => {
              const quoteDesc = q.quoteStatusDescription ? q.quoteStatusDescription : "No Status";
              return quoteDesc.toLowerCase() === desc.toLowerCase();
            });
            if (quote) {
              quotesDataOrdered.push(quote);
            }
          });

          this.quotesData = quotesDataOrdered;
        },
        (error) => {
          // console.log(error);
       });
    }
    catch (e) {
      this.feedback.showToast(FeedbackMessage.GENERIC_FAILURE.toString(), "error");
    }
  }

  public openQuotes(quote) {
    this.nav.push("MyQuotes", {
       params: {
         search: true,
         statusId: quote.quoteStatusTypeId,
       },
    });
  }

}



