import { Component, Output, EventEmitter, Input } from "@angular/core";
import { AuthService } from "../../../../providers/auth.service";
import moment from "moment";
import { Platform } from "ionic-angular";

/**
 * Generated class for the PiplineFilterComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: "pipeline-filter",
  templateUrl: "pipeline-filter.html",
})
export class PipelineFilterComponent {

  @Output() apply = new EventEmitter<any>();
  @Input("showFilter") showFilter: boolean;
  private scoreCardType: string = "Funded";
  private scoreCardDateType: string = "YTD";
  private fromDate: any;
  private toDate: any;
  private dateTimePickerFrom: any;
  private dateTimePickerTo: any;
  private now: any;
  private dateGaurd: any;

  constructor( private platform: Platform) {
    this.now = moment();
    this.dateGaurd = {
      max: this.now.format("YYYY-MM-DD"),
      min: "",
    };
    this.toDate = this.now.format("YYYY-MM-DD");
    this.ytd();
    this.onApply();
  }

  private all() {
    this.scoreCardType = "All";
  }

  private activePipeline() {
    this.scoreCardType = "Active Pipeline";
  }

  private funded() {
    this.scoreCardType = "Funded";
  }

  private adverseActioned() {
    this.scoreCardType = "Adverse Actioned";
  }

  private notLocked() {
    this.scoreCardType = "Not Locked";
  }

  private locked() {
    this.scoreCardType = "Locked";
  }

  private ytd() {
    this.scoreCardDateType = "YTD";
    this.fromDate = moment().startOf("year").format("YYYY-MM-DD");
    this.updateDateGaurds();
    this.toDate = this.now.format("YYYY-MM-DD");
    this.deSelectDateTimePicker();
  }

  private mtd() {
    this.scoreCardDateType = "MTD";
    this.fromDate = moment().startOf("month").format("YYYY-MM-DD");
    this.updateDateGaurds();
    this.toDate = this.now.format("YYYY-MM-DD");
    this.deSelectDateTimePicker();
  }

  private threeMonths() {
    this.scoreCardDateType = "3 Months";
    this.fromDate = moment().subtract(3, "months").startOf("month").format("YYYY-MM-DD");
    this.updateDateGaurds();
    this.toDate = this.now.format("YYYY-MM-DD");
    this.deSelectDateTimePicker();
  }

  private sixMonths() {
    this.scoreCardDateType = "6 Months";
    // tslint:disable-next-line:no-magic-numbers
    this.fromDate = moment().subtract(6, "months").startOf("month").format("YYYY-MM-DD");
    this.updateDateGaurds();
    this.toDate = this.now.format("YYYY-MM-DD");
    this.deSelectDateTimePicker();
  }

  private twelveMonths() {
    this.scoreCardDateType = "12 Months";
    // tslint:disable-next-line:no-magic-numbers
    this.fromDate = moment().subtract(12, "months").startOf("month").format("YYYY-MM-DD");
    this.updateDateGaurds();
    this.toDate = this.now.format("YYYY-MM-DD");
    this.deSelectDateTimePicker();
  }

  private fromDateSelect(event) {
    this.scoreCardDateType = "";
    this.dateTimePickerFrom = "From";
    this.dateTimePickerTo = "To";
    this.fromDate = event;
    this.updateDateGaurds();
    this.toDate = moment(this.fromDate).add(1, "years").isAfter(this.now) ?
      this.now.format("YYYY-MM-DD") : moment(this.fromDate).add(1, "years").format("YYYY-MM-DD");
  }

  private toDateSelect(event) {
    this.scoreCardDateType = "";
    this.dateTimePickerFrom = "From";
    this.dateTimePickerTo = "To";
    // if (moment(event).isBefore(this.fromDate)) {
    //   // tslint:disable-next-line:no-magic-numbers
    //   // this.feedbackService.showToast(FeedbackMessage.FROM_DATE_VALIDATION.toString(), "error", 5000);
    // }
    this.toDate = event;
  }

  private onApply() {
    const params = {
      type: this.scoreCardType,
      range: this.scoreCardDateType,
      from: this.fromDate,
      to: this.toDate,
    };
    // if (moment(this.toDate).isBefore(this.fromDate)) {
    //   // tslint:disable-next-line:no-magic-numbers
    //   this.feedbackService.showToast(FeedbackMessage.FROM_DATE_VALIDATION.toString(), "error", 5000);
    // } else {
    this.apply.emit(params);
    // }
  }

  private deSelectDateTimePicker() {
    this.dateTimePickerFrom = null;
    this.dateTimePickerTo = null;
  }

  private updateDateGaurds() {
    this.dateGaurd.min = this.fromDate;
  }
}



