import { NgModule } from "@angular/core";
import { IonicPageModule } from "ionic-angular";
import { MyPipelineDashboard } from "./my-pipeline";
import { PipelineSummaryDashboardModule } from "./pipeline-summary/pipeline-summary.module";
import { PipelineStatusDashboardModule } from "./pipeline-status/pipeline-status.module";
import { PipelineMilestoneDashboardModule } from "./pipeline-milestone/pipeline-milestone.module";

@NgModule({
  declarations: [
    MyPipelineDashboard,
  ],
  imports: [
    IonicPageModule.forChild(MyPipelineDashboard),
    PipelineStatusDashboardModule,
    PipelineMilestoneDashboardModule,
    PipelineSummaryDashboardModule,
  ],
  exports: [
    MyPipelineDashboard,
  ],
})
export class MyPipelineDashboardModule {}



