import { Component, Input} from "@angular/core";
import { reorderArray, ActionSheetController, NavController, ModalController } from "ionic-angular";
import { AlertModel } from "../../../common/model/dashboard/alert.model";

/**
 * Generated class for the AlertsDashboardComponent component.
 *
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: "alerts-dashboard",
  templateUrl: "alerts-dashboard.html",
})
export class AlertsDashboard {

  @Input("alerts") alerts: AlertModel[] = null;
  isReordering: boolean = true;
  isAlertsModelEmpty: boolean = false;

  constructor(private actionSheetCtrl: ActionSheetController,
              private modalCtrl: ModalController,
              private nav: NavController ) {
  }

  ngAfterContentChecked() {
    if (!this.alerts || this.alerts.length === 0) {
      this.isAlertsModelEmpty = true;
    } else {
      this.isAlertsModelEmpty = false;
    }
  }

  reorderStart() {
    if (this.isReordering) {
      return;
    }
    this.isReordering = true;
    // console.log(this.isReordering);
  }

  reorderDone() {
    this.isReordering = false;
    // console.log(this.isReordering);
  }

  reorder(indexes: any) {
    this.alerts = reorderArray(this.alerts, indexes);
  }

  private hideCard(alert: AlertModel) {
    // console.log("hide the card!");
  }

  private drilldown(alert: AlertModel) {
    if (alert.drilldown.page) {
      this.nav.push(alert.drilldown.page, {
        params: alert.drilldown.params,
      });
    }
  }

}



