import { Component, ElementRef, ViewChild, Input } from "@angular/core";
import { IonicPage } from "ionic-angular";
import { Chart } from "chart.js";
import R from "ramda";
import { StatsService } from "../../../../providers/loans/stats.service";
import { Filters } from "../../../../common/utils/filters";

@Component({
  selector: "performance-type",
  templateUrl: "performance-type.html",
})
export class PerformanceType {

  @ViewChild("chart1") chart1: ElementRef;
  @Input("params") inputParams: any = null;
  countChartType = false;
  range: string = "ytd";
  summary: any = null;
  tmpSummary: any = {
    month: "",
    my: {
      name: "My Performance",
      amount: "",
      unit: "",
    },
    avg: {
      name: "Avg. Performance",
      amount: "",
      unit: "",
    },
  };

  public chart: any;

  public barChartLabels: string[] =
  ["JAN", "FEB", "MAR", "APR", "MAY", "JUN",
  "JUL", "AUG", "SEP", "OCT", "NOV", "DEC"];

  public thisMonth = (new Date()).getMonth() + 1;

  public timePeriods = [
    { name: "YTD", value: this.thisMonth },
    { name: "6 Months", value: 6 },
    { name: "12 Months", value: 12 },
  ];
  public timePeriod = this.timePeriods[0].value;
  public chartOptions = [
    { name: "Funded & Closed", value: 1 },
    { name: "Adversed Actioned", value: 2 },
    { name: "Originated", value: 3 },
  ];
  public chartOption = this.chartOptions[0].value;
  private amountData = [
    {
      data: [],
      label: "Avg. Performance",
      borderColor: "rgb(255, 165, 115)",
      backgroundColor: "rgb(255, 165, 115)",
      fill: false,
      lineTension: 0.5,
      type: "line",
      pointRadius: 3,
      pointHoverRadius: 3,
      pointHoverBorderColor: "rgb(255, 80, 3)",
      pointBackgroundColor: "rgb(255, 165, 115)",
      animation: true,
    },
    {
      data: [],
      label: "My Performance",
      backgroundColor: "rgb(255, 80, 3)",
      borderColor: "rgb(255, 80, 3)",
      animation: true,
    },
    {
      data: [],
      label: "Unfilled",
      backgroundColor: "rgba(242, 243, 245,.5)",
      borderColor: "rgba(242, 243, 245,.5)",
      animation: true,
    },
  ];

  private unitsData = [
    {
      data: [],
      label: "Avg. Units",
      borderColor: "rgb(255, 165, 115)",
      backgroundColor: "rgb(255, 165, 115)",
      fill: false,
      lineTension: 0.5,
      type: "line",
      pointRadius: 3,
      pointHoverRadius: 3,
      pointHoverBorderColor: "rgb(255, 80, 3)",
      pointBackgroundColor: "rgb(255, 165, 115)",
      animation: true,
    },
    {
      data: [],
      label: "My Units",
      backgroundColor: "rgb(255, 80, 3)",
      borderColor: "rgb(255, 80, 3)",
      animation: true,
    },
    {
      data: [],
      label: "Unfilled",
      backgroundColor: "rgba(242, 243, 245,.5)",
      borderColor: "rgba(242, 243, 245,.5)",
      animation: true,
    },
  ];

  private barChartData = this.amountData;
  private activeData = this.amountData;

  private labels = this.barChartLabels.slice(0, this.timePeriod);

  private formatCurrencyShort = Filters.formatCurrencyShort;
  private formatMonthLong = Filters.getFullMonth;

  private maxAmount = null;
  private maxCount = null;

  private intervalParam = { intervalType: "YTD", intervalValue: undefined };

  private chartConfig = {
    type: "bar",
    data: {
      labels: this.labels,
      datasets: this.barChartData,
    },
    options: {
      tooltips: {
        enabled: false,
      },
      layout: {
        padding: 5,
      },
      responsive: true,
      legend: {
        display: true,
        position: "bottom",
        onClick: null,
        labels: {
          boxWidth: 10,
          usePointStyle: true,
          fontSize: 9,
          filter: (legendItem, data) => {
            if (legendItem && legendItem.text === "Unfilled") {
              return false;
            }
            return true;
          },
        },
        maintainAspectRatio: true,
        animation: false,
      },
      scales: {
        yAxes: [
          {
            stacked: true,
            position: "left",
            id: "data",
            gridLines: {
              display: false,
              drawBorder: false,
            },
            ticks: {
            },
          },
        ],
        xAxes: [{
          stacked: true,
          display: true,
          barThickness: 15,
          ticks: {
            fontSize: 10,
          },
          gridLines: {
            display: false,
            drawBorder: false,
          },
        }],
      },
      onClick: (event, array) => {
        if (array[0] && array[1]) {
          const index1 = array[0]._index;
          const index2 = array[0]._index;
          this.summary = this.tmpSummary;
          this.summary.month = this.formatMonthLong(this.labels[index1]);
          this.summary.my.amount = this.formatCurrencyShort(this.amountData[1].data[index1]);
          this.summary.avg.amount = this.formatCurrencyShort(this.amountData[0].data[index2]);
          this.summary.my.unit = this.unitsData[1].data[index1];
          this.summary.avg.unit = this.unitsData[0].data[index2];
        }
      },
    },
  };

  constructor(
    public service: StatsService,
    public filter: Filters,
  ) {
    Chart.elements.Rectangle.prototype.draw = function() {

      const ctx = this._chart.ctx;
      const vm = this._view;
      let left, right, top, bottom, signX, signY, borderSkipped, radius;// tslint:disable-line
      let borderWidth = vm.borderWidth;
      // Set Radius Here
      // If radius is large enough to cause drawing errors a max radius is imposed
      const cornerRadius = 20;

      if (!vm.horizontal) {
          // bar
          left = vm.x - vm.width / 2;
          right = vm.x + vm.width / 2;
          top = vm.y;
          bottom = vm.base;
          signX = 1;
          signY = bottom > top ? 1 : -1;
          borderSkipped = vm.borderSkipped || "bottom";
      } else {
          // horizontal bar
          left = vm.base;
          right = vm.x;
          top = vm.y - vm.height / 2;
          bottom = vm.y + vm.height / 2;
          signX = right > left ? 1 : -1;
          signY = 1;
          borderSkipped = vm.borderSkipped || "left";
      }

      // Canvas doesn't allow us to stroke inside the width so we can
      // adjust the sizes to fit if we're setting a stroke on the line
      if (borderWidth) {
          // borderWidth shold be less than bar width and bar height.
          const barSize = Math.min(Math.abs(left - right), Math.abs(top - bottom));
          borderWidth = borderWidth > barSize ? barSize : borderWidth;
          const halfStroke = borderWidth / 2;
          // Adjust borderWidth when bar top position is near vm.base(zero).
          const borderLeft = left + (borderSkipped !== "left" ? halfStroke * signX : 0);
          const borderRight = right + (borderSkipped !== "right" ? -halfStroke * signX : 0);
          const borderTop = top + (borderSkipped !== "top" ? halfStroke * signY : 0);
          const borderBottom = bottom + (borderSkipped !== "bottom" ? -halfStroke * signY : 0);
          // not become a vertical line?
          if (borderLeft !== borderRight) {
              top = borderTop;
              bottom = borderBottom;
          }
          // not become a horizontal line?
          if (borderTop !== borderBottom) {
              left = borderLeft;
              right = borderRight;
          }
      }

      ctx.beginPath();
      ctx.fillStyle = vm.backgroundColor;
      ctx.strokeStyle = vm.borderColor;
      ctx.lineWidth = borderWidth;

      // Corner points, from bottom-left to bottom-right clockwise
      // | 1 2 |
      // | 0 3 |
      const corners = [
          [left, bottom],
          [left, top],
          [right, top],
          [right, bottom],
      ];

      // Find first (starting) corner with fallback to 'bottom'
      const borders = ["bottom", "left", "top", "right"];
      let startCorner = borders.indexOf(borderSkipped, 0);
      if (startCorner === -1) {
          startCorner = 0;
      }

      function cornerAt(index) {
        // tslint:disable-next-line:no-magic-numbers
          return corners[(startCorner + index) % 4];
      }

      // Draw rectangle from 'startCorner'
      let corner = cornerAt(0);
      ctx.moveTo(corner[0], corner[1]);
      // tslint:disable-next-line:no-magic-numbers
      for (let i = 1; i < 4; i++) {
          corner = cornerAt(i);
          let nextCornerId = i + 1;
          // tslint:disable-next-line:no-magic-numbers
          if (nextCornerId === 4){
              nextCornerId = 0;
          }

          const nextCorner = cornerAt(nextCornerId);

          const width = corners[2][0] - corners[1][0];
          const height = corners[0][1] - corners[1][1];
          const x = corners[1][0];
          const y = corners[1][1];

          radius = cornerRadius;

          // Fix radius being too large
          if (radius > height / 2){
              radius = height / 2;
          }
          if (radius > width / 2){
              radius = width / 2;
          }

          ctx.moveTo(x + radius, y);
          ctx.lineTo(x + width - radius, y);
          ctx.quadraticCurveTo(x + width, y, x + width, y + radius);
          ctx.lineTo(x + width, y + height - radius);
          ctx.lineTo(x + width, y + height, x + width - radius, y + height);
          ctx.lineTo(x + radius, y + height);
          ctx.lineTo(x, y + height, x, y + height - radius);
          ctx.lineTo(x, y + radius);
          ctx.quadraticCurveTo(x, y, x + radius, y);

      }

      ctx.fill();
      if (borderWidth) {
          ctx.stroke();
      }
    };
  }

  public getactiveChartType() {
    if (this.countChartType === true) {
      return "Unit";
    } else {
      return "Amount";
    }
  }

  ngOnInit() {
    this.changeChartType(this.inputParams.value);
  }

  public getFundedAndClosedLoans(data) {
    const count = {};
    const amount = {};

    const currentYear = (new Date()).getFullYear();
    const currentMonth = (new Date()).getMonth();

    let startingMonth = data[0] ? data[0].yearMonth % 100 : 0;
    const startingYear = data[0] ? Math.round(data[0].yearMonth / 100) : currentYear;

    const intervalType = this.intervalParam.intervalType;
    const intervalValue = this.intervalParam.intervalValue;

    data.forEach((x) => {
      const month = x.yearMonth % 100;
      const year = Math.round(x.yearMonth / 100);
      const monthLabel = this.barChartLabels[month];
      count[monthLabel + year] = x.count;
      amount[monthLabel + year] = x.loanAmt;
    });

    if (intervalType === "YTD") {
      for (let i = 0; i <= currentMonth; i++) {
        const month = this.barChartLabels[i];
        const year = currentYear;
        count[month + year] = count[month + year] ? count[month + year] : 0;
        amount[month + year] = amount[month + year] ? amount[month + year] : 0;
      }
    } else if (intervalType === "MONTH") {
      startingMonth = currentMonth - intervalValue + 1;
      if (startingMonth < 0) {
        // tslint:disable-next-line:no-magic-numbers
        for (let i = 12 + startingMonth; i < 12; i++) {
          const month = this.barChartLabels[i];
          const year = currentYear - 1;
          count[month + year] = count[month + year] ? count[month + year] : 0;
          amount[month + year] = amount[month + year] ? amount[month + year] : 0;
        }
        startingMonth = 0;
      }
      for (let i = startingMonth; i <= currentMonth; i++) {
        const month = this.barChartLabels[i];
        const year = currentYear;
        count[month + year] = count[month + year] ? count[month + year] : 0;
        amount[month + year] = amount[month + year] ? amount[month + year] : 0;
      }
    }
    return { count, amount };
  }

  public createFundedLoansData(myFundedLoans, avgFundedLoans) {
    this.unitsData[0].data = [];
    this.unitsData[1].data = [];
    this.unitsData[2].data = [];

    this.amountData[0].data = [];
    this.amountData[1].data = [];
    this.amountData[2].data = [];

    const myUnitsData = this.unitsData[1].data;
    const myUnFilledUnitsData = this.unitsData[2].data;
    this.unitsData[2].animation = false;
    const avgUnitsData = this.unitsData[0].data;

    const myAmountData = this.amountData[1].data;
    const myUnfilledAmountData = this.amountData[2].data;
    this.amountData[2].animation = false;
    const avgAmountData = this.amountData[0].data;

    const myCount = myFundedLoans.count;
    const myAmount = myFundedLoans.amount;

    const avgCount = avgFundedLoans.count;
    const avgAmount = avgFundedLoans.amount;

    this.maxAmount = this.getLargest(myAmount, avgAmount);
    this.maxCount = this.getLargest(myCount, avgCount);
    const unitsMonthLabels = [];

    const amountMonthLabels = [];

    const currentYear = (new Date()).getFullYear();
    const currentMonth = (new Date()).getMonth();

    const intervalType = this.intervalParam.intervalType;
    const intervalValue = this.intervalParam.intervalValue;

    let startingMonth = 0;
    if (intervalType === "MONTH") {
      startingMonth = currentMonth - intervalValue + 1;
      if (startingMonth < 0) {
        // tslint:disable-next-line:no-magic-numbers
        for (let i = 12 + startingMonth; i < 12; i++) {
          const month = this.barChartLabels[i];
          const year = currentYear - 1;

          unitsMonthLabels.push(month);
          myUnitsData.push(myCount[month + year]);
          myUnFilledUnitsData.push(this.maxCount - myCount[month + year]);
          avgUnitsData.push(avgCount[month + year]);

          amountMonthLabels.push(month);
          myAmountData.push(myAmount[month + year]);
          myUnfilledAmountData.push(this.maxAmount - myCount[month + year]);
          avgAmountData.push(avgAmount[month + year]);

        }
        startingMonth = 0;
      }
    }

    for (let i = startingMonth; i <= currentMonth; i++) {
      const month = this.barChartLabels[i];
      const year = currentYear;

      unitsMonthLabels.push(month);
      myUnitsData.push(myCount[month + year]);
      myUnFilledUnitsData.push(this.maxCount - myCount[month + year]);
      avgUnitsData.push(avgCount[month + year] ? avgCount[month + year] : 0);

      amountMonthLabels.push(month);
      myAmountData.push(myAmount[month + year]);
      myUnfilledAmountData.push(this.maxAmount - myCount[month + year]);
      avgAmountData.push(avgAmount[month + year]);

    }

    this.labels = unitsMonthLabels;
    this.chartConfig.data.labels = this.labels;
  }

  private getLargest(obj1, obj2) {
    const obj1Array: number[] = Object.keys(obj1).map((key) => {
      return obj1[key];
    });
    const obj2Array = Object.keys(obj2).map((key) => {
      return obj2[key];
    });
    const largest = Math.max(...[...obj1Array, ...obj2Array]);
    const trucatedNumber = Math.trunc(Math.abs(largest));
    // tslint:disable-next-line:no-magic-numbers
    const n = Math.pow(10, trucatedNumber.toString().length - 1);
    const result = Math.ceil(trucatedNumber / n) * n;
    return result;
  }

  private loadBreakdown() {
    this.chart1.nativeElement.height = "350px";
    this.timePeriod = this.thisMonth;
    this.barChartData = this.amountData;
    const data = R.zip(this.barChartData[0].data,
      this.barChartData[1].data);
    this.activeData = [];
    this.activeData = R.zip(this.labels, data);
    this.chart1.nativeElement.scrollLeft = 0;
    this.randomize(this.getactiveChartType());
    this.setSummary();
  }

  private changeChart(chartType) {
    if (chartType === true) {
      this.barChartData = this.unitsData;
      const data = R.zip(
        this.unitsData[1].data,
        this.unitsData[0].data);
      this.activeData = [];
      this.activeData = R.zip(this.labels, data);
      this.randomize(this.getactiveChartType());
    } else {
      this.barChartData = this.amountData;
      const data = R.zip(
        this.amountData[1].data,
        this.amountData[0].data);
      this.activeData = [];
      this.activeData = R.zip(this.labels, data);
      this.randomize(this.getactiveChartType());
    }
  }

  private randomize(type): void {
    const timePeriod = this.timePeriod;
    if (type === "Unit") {
      this.chartConfig.data.datasets = this.unitsData;
      const ticks = this.chartConfig.options.scales.yAxes[0].ticks;
      this.chartConfig.options.scales.yAxes[0].ticks = {
        callback: (value, index, values) => ((Math.floor(value) === value) ? value : null),
        beginAtZero: true,
        max: this.maxCount,
        fontSize: 10,
      };
    }
    if (type === "Amount") {
      this.chartConfig.data.datasets = this.amountData;
      this.chartConfig.options.scales.yAxes[0].ticks = {
        callback: (value, index, values) => this.formatCurrencyShort(value),
        beginAtZero: true,
        max: this.maxAmount,
        fontSize: 10,
      };
    }
    if (this.chart && "destroy" in this.chart) {
      this.chart.destroy();
      this.chart = null;
    }
    this.chart = new Chart(this.chart1.nativeElement, this.chartConfig);
  }

  private changeChartType(chartOption): void {
    let myFundedLoans;
    let avgFundedLoans;
    const intervalType = this.intervalParam.intervalType;
    const intervalValue = this.intervalParam.intervalValue;

    switch (chartOption) {
      case 1:
        this.service.getMyFundedAndClosedLoans(intervalType, intervalValue)
          .subscribe((response) => {
            myFundedLoans = this.getFundedAndClosedLoans(response);
            this.service.getAvgFundedAndClosedLoans(intervalType, intervalValue)
              .subscribe((response1) => {
                avgFundedLoans = this.getFundedAndClosedLoans(response1);
                this.createFundedLoansData(myFundedLoans, avgFundedLoans);
                this.loadBreakdown();
              });
          });
        break;

      case 2:
        this.service.getMyAdversedAndClosedLoans(intervalType, intervalValue)
          .subscribe((response) => {
            myFundedLoans = this.getFundedAndClosedLoans(response);
            this.service.getAvgAdversedAndClosedLoans(intervalType, intervalValue)
              .subscribe((response1) => {
                avgFundedLoans = this.getFundedAndClosedLoans(response1);
                this.createFundedLoansData(myFundedLoans, avgFundedLoans);
                this.loadBreakdown();
              });
          });
        break;

      case 3:
        this.service.getMyOriginatedLoans(intervalType, intervalValue)
          .subscribe((response) => {
            myFundedLoans = this.getFundedAndClosedLoans(response);
            this.service.getAvgOriginatedLoans(intervalType, intervalValue)
              .subscribe((response1) => {
                avgFundedLoans = this.getFundedAndClosedLoans(response1);
                this.createFundedLoansData(myFundedLoans, avgFundedLoans);
                this.loadBreakdown();
              });
          });
        break;
    }

  }

  private changeIntervalParam(intervalType, intervalValue) {
    this.intervalParam.intervalType = intervalType;
    this.intervalParam.intervalValue = intervalValue;
    this.changeChartType(this.inputParams.value);
  }

  private setSummary() {
    this.summary = this.tmpSummary;
    if (this.labels && this.labels.length > 0) {
      const currentMonth = this.labels.length - 1;
      this.summary.month = this.formatMonthLong(this.labels[currentMonth]);
      this.summary.my.amount = this.formatCurrencyShort(this.amountData[1].data[currentMonth]);
      this.summary.avg.amount = this.formatCurrencyShort(this.amountData[0].data[currentMonth]);
      this.summary.my.unit = this.unitsData[0].data[currentMonth];
      this.summary.avg.unit = this.unitsData[1].data[currentMonth];
    }
  }
}



