import { Component } from "@angular/core";
import { HomeService } from "../../../../providers/home.service";
import { Filters } from "../../../../common/utils/filters";

@Component({
  selector: "pipeline-summary-dashboard",
  templateUrl: "pipeline-summary.html",
})
export class PipelineSummaryDashboard {

  public totalAmount: number = 0;
  public totalCount: number = 0;

  public activeCategories = [
    "lead",
    "prospect",
    "application",
    "setup",
    "processing",
    "underwriting",
    "approved",
    "clear to close",
    "closing",
  ].join(",");

  formatCurrency = Filters.currency;

  constructor(
    private homeService: HomeService,
  ) {

  }

  ngOnInit() {
    this.homeService.getPipelineCounts().subscribe(
      (response) => {
        let data = response.data.filter((d) => d.count > 0);
        let base = this;
        data = data.filter((d) => ( base.activeCategories.indexOf(d.desc.toLowerCase().trim()) > 0));
        this.totalAmount = data.reduce((tot, d) => tot + d.amount, 0);
        this.totalCount = data.reduce((tot, d) => tot + d.count, 0);
      },
      (error) => {
        // console.log(error);
      });
  }

}



